import React from 'react';
import { Link } from "react-router-dom";
import { TodoContext } from "../contexts/todoContext"

function SolvedTodos() {
  return (
    <TodoContext.Consumer>{(context) => {
      const {todos, setTask} = context;
      return(
        <div>
          <p>Your solved taks</p>
          { todos.map(oneTodo => {
            return (
              oneTodo.solved === true ? (
                 <div className="task-card" key={oneTodo._id}>
                 <p className="task-item"><span>Title</span>: {oneTodo.name}</p>
                  <p className="task-item"><span>Description</span>: {oneTodo.description}</p>
                  <p className="task-item"><span>Created on</span>: {oneTodo.createdTimestamp}</p>
                  <p className="task-item"><span>Solved on</span>: {oneTodo.solvedTimestamp}</p>
                   <Link to={`/update/${oneTodo._id}`}>
                     <button className="task-button" onClick={() => {setTask(oneTodo._id)}}>Update task</button>
                   </Link>
                 </div>
              ) : ( null)
            ) 
          })}
        </div>
      )
    }}
    </TodoContext.Consumer>
  )
}

export default SolvedTodos;
